@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Space+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap");
.card {
  width: 300px;
  height: 500px;
  background: #f9f7f7de;
  backdrop-filter: blur(17px);
  padding: 10px;
  margin-bottom: 20px;
}
.albumPhoto {
  width: 100%;
  height: auto;
}


.card-text p {
  color: #493729;
  font-family: sans-serif;
  font-size: 18px;
  font-style: normal;
  text-align: left;
  font-weight: 800;
  line-height: normal;
  height: 60px;
}
.card-text span {
  color: #000000;
  font-family: sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.func-buttons {
  display: flex;
  /* margin: 20px; */
  justify-content: space-around;
  /* align-items: center; */
}
.editicons {
  padding:0.6em 2em;
  border-radius: 8px;
  color:#fff;
  background-color:#1976d2;
  font-size:1.1em;
  border:0;
  cursor:pointer;
  margin:1em;
}
.deleteicons {
  border-radius: 8px;
  color:#fff;
  background-color:#1976d2;
  font-size:1.1em;
  border:0;
  cursor:pointer;
  margin:1em;
  background-color: #ff3f14;
  text-align: center;
}
